/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"
import Icon from '@mdi/react';
import { mdiHelpCircleOutline } from '@mdi/js';

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  console.log('DATA', data); // eslint-disable-line

  return (
    <React.Fragment>
      <div
        style={{
          margin: `0 auto`,
          maxWidth: 960,
          padding: `0 1.0875rem 1.45rem`,
        }}
      >
        <main>{children}</main>
        <footer style={{
          marginTop: `2rem`
        }} className="text-center">
          <a className="no-decoration text-lg" target="_blank" rel="noreferrer noopener" href="https://www.flickr.com/photos/14835854@N03/14811447395/" title="What's on the background?">
            <Icon path={ mdiHelpCircleOutline } size="20px" />
          </a>
        </footer>
      </div>
    </React.Fragment>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
